/* eslint-disable max-len */
import { Box } from '@mui/material';
import { useNavigate, useParams } from 'react-router';
import ArrowBack from '@mui/icons-material/ArrowBack'

import Label from 'src/components/Label';
import { Gallary } from '../../components/Gallary';
import { Project, yearsData } from './data';

export function PortfolioItem() {
  const navigate = useNavigate()
  const { id } = useParams()
  const data = yearsData[id] || { title: '-', projects: [{ title: '', desc: '' }] }
  const { title, projects } = data

  return (
    <Box sx={{ marginTop: '4px', padding: 2 }}>
      <h1 style={{
        zIndex: 0,
        textAlign: 'right',
        position: 'absolute',
        width: '100%',
        top: '-5px',
        right: '84px',
        color: '#3f445e'
      }}
      >
        {title}
      </h1>
      <ArrowBack
        sx={{
          fontSize: 45, color: '#fff', position: 'relative', zIndex: 0
        }}
        onClick={() => navigate('/portfolio')}
      />

      {
        projects.map((pr: Project) => (
          <Box sx={{ mb: 10 }}>
            <h1>{pr.title}</h1>
            <Box
              component="p"
              sx={{ marginTop: 4, marginBottom: 3 }}
            >
              {pr.desc}
            </Box>
            <Box sx={{
              display: 'flex', gap: 1, mb: 3, flexWrap: 'wrap'
            }}
            >
              {pr.tags.map((tag) => <Label>{tag}</Label>)}
            </Box>
            <Box>
              <Gallary
                key="t11"
                images={pr.images}
              />
            </Box>
            <Box
              component="p"
              sx={{ marginTop: 3 }}
            >
              {pr.desc2}
            </Box>
          </Box>
        ))

      }

    </Box>
  )
}

export default PortfolioItem
